<template>
<section class="center-product">
  <div class="container mx-auto p-2">
    <div class="w-full h-auto">
      <div class="flex flex-row items-center justify-start">
        <div @click="getNumbers(1)" :class="{red : counter===1, gray : counter===3}">
          <button @click="tabs='AttrsBase'" type="button">
            <font-awesome-icon :icon="['fas','tag']" class="text-sm font-bold"></font-awesome-icon>
            <span class=" font-bold mx-1 text-sm p-2">ویژگی ها</span>
          </button>
        </div>
        <div @click="getNumbers(2)" :class="{red : counter===2, gray : counter===1}">
          <button @click="tabs='CommandBase'" type="button">
            <font-awesome-icon :icon="['fas','comments']" class="text-sm font-bold"></font-awesome-icon>
            <span class=" font-bold mx-1 text-sm p-2">نظرات(0)</span>
          </button>
        </div>
        <div @click="getNumbers(3)" :class="{red : counter===3, gray : counter===1}">
          <button @click="tabs='QuestionBase'" type="button">
            <font-awesome-icon :icon="['fas','question']" class="text-sm font-bold"></font-awesome-icon>
            <span class="font-bold mx-1 text-sm p-2">پرسش و پاسخ</span>
          </button>
        </div>
      </div>
      <keep-alive>
        <component :is="tabs"></component>
      </keep-alive>
    </div>
  </div>
</section>
</template>

<script>
import{ref} from 'vue';
import AttrsBase from "@/components/widget/AttrsBase";
import CommandBase from "@/components/widget/CommandBase";
import QuestionBase from "@/components/widget/QuestionBase";
import {FontAwesomeIcon} from "@fortawesome/vue-fontawesome";
export default {
  name: "ProductBase",
  components: {FontAwesomeIcon,AttrsBase,CommandBase,QuestionBase},
  setup(){
    const counter=ref(1)
    const tabs=ref('AttrsBase')
    function getNumbers(data){
      counter.value=data
    }
    return{
      tabs,counter,getNumbers,
    }
  }
}
</script>

<style scoped>
.red{
  background-color: #fff;
  color: red;
}
.gray{
  color: gray;
}
</style>